.home-content{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100vh;
    width:100vw;
    margin-top: 35%;
}  
h2 {
    text-align: center;
}
a {
    text-decoration: none;
    color: black;
}
h4 {
    font-weight:bold;
    font-family: 'Lobster', cursive;
}
.meal-nav {
   height: 55vh;
   width: 70vw;
   display: flex;
   flex-direction: column;
   justify-content: space-around;
   align-items: center;
  
}
.meal-nav img {
    height: 50vh;
    width: 70vw;
    box-shadow: 10px 10px 5px grey;
}
.meal-nav-description {
    height: 20vh;
    width: 30vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    border: solid 2px white;
    position: relative;
    top: 50px;
    left: 1%;
    border-radius: 10px;
    background-color: rgba(152, 199, 76, 0.979);
}
.dropdown-content {
    position: fixed;
    background-color: rgba(152, 199, 76, 0.979);
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    top:80px;
    right:0px;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 20vh;
    width: 15vw;
  }
  @media only screen and (max-width : 600px) {
    .home-content{
        margin-top: 80%;
    }
    .meal-nav {
        height: 50vh;
        width: 90vw;
    }
    .meal-nav img {
        height: 45vh;
        width: 90vw;
    }
    .meal-nav-description {
        top: 55px;
        left: 0;
        width: 60vw;
    }
  }
  @media only screen and (max-width : 400px) {
    .home-content{
        margin-top: 110%;
    }
  }
.meal-plan-content{
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100vh;
    width: 100vw;
}
.search-meal-content {
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height: 10vh;
    width: 30vw;
}
.meals{
    height: 80vh;
    width: 60vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-shadow: 10px 10px 10px 10px #aaaaaa;
    padding: 10px;
}
.meals-heading {
    height: 10vh;
}
.meal-content {
    height: 30vh;
    width: 50vw;
    display: flex;
    flex-direction: row;
   
}
.meal-content img {
    height: 18vh;
    width: 15vw;
}
.meal-description {
    display: flex;
    flex-direction: column;
    width: 30vw;
    position: relative;
    left: 20px;
    font-family: 'Lobster', cursive;
}
h3 {
    font-size: 30px;
    font-weight:bold;
    font-family: 'Lobster', cursive;
}
h5 {
    font-family: 'PT Sans Narrow', sans-serif;
    font-weight: bold;
}
@media only screen and (max-width : 600px) {
    .meal-plan-content{
        margin-top: 10%;
      }
      .meals{
        width: 90vw;
      }
      .meal-content{
          width:85vw;
      }
      .meal-content img{
          width:30vw;
      }
      .meal-description {
        width: 50vw;  
      }
}
@media only screen and (max-width : 400px) {
    .meal-content img{
        height:15vh;
        width:30vw;
    }

  }
.workout-plan-content{
    height:100vh;
    width:100vw;
    margin-top: 3%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.home-icon{
    height: 7vh;
    width: 4vw;
    position: fixed;
    top: 10px;
    z-index: 5;
    right: 50px;
    
}
.workout-plan{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 98vh;
    width:90vw;
    /* box-shadow: 10px 10px 10px 10px #aaaaaa; */
}
.loading{
    height: 10vh;
    width: 8vw;
}
.workout-video-content{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width:88vw;
    height: 95vh;
}
.workout-video-section{
    width: 20vw;
}
.workout-video-disp {
    width: 60vw;
    height: 90vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
}
.working-video {
    width: 60vw;
    height: 80vh;
    padding: 5px 30px;
}
.video-play {
    height:65vh;
    width: 56vw;
   
}
.video-play iframe{
    height:65vh;
    width: 56vw;
}
.video-desc{
    height:20vh;
    width: 56vw; 
}
.header-video{
    font-family: 'PT Sans Narrow', sans-serif;
    font-weight: bold; 
}
.loading-content {
    font-size: 25px;
    font-family: 'Baloo', cursive;
}

@media only screen and (max-width : 600px) {
    .home-icon{
        width:10vw;
    }
    .workout-plan-content{
        margin-top:30%;
    }
    .workout-video-content{
        flex-direction: column;
    }
    .workout-video-section{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        height: 80vh;
        width:63vw;
        margin-top:10%;
    }
    .working-video {
        width: 98vw;
        height:74vh;
    }
    .workout-video-disp {
        height: 50vh;
        width:90vw;
    }
    .video-play {
        height:45vh;
        width: 80vw;
       
    }
    .video-play iframe{
        height:38vh;
        width: 80vw;
        position:relative;
        top:97px;
    }
    .workout-video{
        height:20vh;
    }
    .workout-video img{
        height:15vh;
        width:60vw;
    }
    .video-desc{
        height:20vh;
        width: 80vw; 
        margin-top:15%;
    }
    h4{
        font-size:14px;
    }
    p{
        font-size:11px;
    }
}
@media only screen and (max-width : 400px) {
}

.health-check-section{
    height: 100vh;
    width:100vw;
    margin-top: 5%;
}
.health-check-section h3{
   text-align:center;
}
.health-check-content{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height:100vh;
    width:100vw;
    margin-top:-5%;
}
.bmi-form,.bmi-result{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height:60vh;
   
    border-radius:12px;
    box-shadow: 10px 10px 10px 10px #aaaaaa;
}
.bmi-form{
    width:30vw;
}
.bmi-result{
  padding: 5px;
  width:60vw;
}
.bmi-result-section{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.bmi-result-parts{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
}
.bmi-result-part1{
    width: 30vw;
}
.bmi-result-part2{
    width: 15vw;
}
.bmi-result-part3{
    width: 15vw;
}
.bmi-result-parts label,.bmi-result-parts p {
    font-family: 'Lobster', cursive;
}
.bmi-result-parts label{
    font-size:15px;
}
.bmi-result-parts h4{
    text-align: center;
    font-family: cursive;
    font-weight: bold;
}
form {
    height:60vh;
    width:30vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bmi-field-groups {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height:50vh;
    width:30vw;
}
.bmi-fields {
    height:10vh;
    width:25vw;
   
}
.bmi-fields label{
    width: 5vw;
    font-size: 18px;
}
.bmi-fields input{
    width: 15vw;
}
select {
    width: 15vw;
    height:5vh;
    position: relative;
    left: 85px; 
}
.submit {
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader-section{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
@media only screen and (max-width : 600px) {
    .health-check-section{
        margin-top: 30%;
    }
    .health-check-content{
        flex-direction: column;
    }
    .bmi-form{
        width:80vw;
        margin-top: 4%;
        height:55vh;
    
    }
    form {
        width:80vw;
        height:38vh;
    }
    .bmi-field-groups {
        width:80vw;
        height: 38vh;
    }
    .bmi-fields {
        width:70vw;
    }
    .bmi-fields input, select{
        width: 30vw;
        left:192px;
        height: 4vh;
    }
    .bmi-result{
      width:97vw;
      margin-top: 5%;
    }
    .bmi-result-part1{
        width: 50vw;
    }
    .bmi-result-part2{
        width: 21vw;
    }
    .bmi-result-part3{
        width: 21vw;
    }
}
